import React, { useState } from "react";
import "./Navbar.css";
import {FiMenu,FiX} from 'react-icons/fi'
import { Link } from "react-scroll";

function Navbar() {
  const [open, setOpen] = useState(false);
  const handleClick =()=> setOpen(!open);
  const closeMenu = ()=> setOpen(false);
  return (
    <div className="header">
      <div className="header-left">
        <h1>
          Develp<span>er</span>
        </h1>
      </div>
      <div className={open ? "header-right active" : "header-right"}>
        <Link to="home" smooth={true} duration={500} className="nav-item" onClick={closeMenu}>
          <h4>About</h4>
        </Link>
        <Link to="skills" smooth={true} duration={500} className="nav-item" onClick={closeMenu}>
          <h4>Skills</h4>
        </Link>
        <Link to="project" smooth={true} duration={500} className="nav-item" onClick={closeMenu}>
          <h4>Projects</h4>
        </Link>
        <Link to="exp" smooth={true} duration={500} className="nav-item" onClick={closeMenu}>
          <h4>Experience</h4>
        </Link>
        <Link to="contact" smooth={true} duration={500} className="nav-item" onClick={closeMenu}>
          <h4>Contact</h4>
        </Link>
        {/* <h4 className='header-right-button'>Join with me</h4> */}
        
      </div>
      <div className="nav-icon" onClick={handleClick}>
        {open ? <FiX/>:<FiMenu/>}
        </div>
    </div>
  );
}

export default Navbar;
